body{
    margin: 0;
    background: radial-gradient(#3e3d3d, #000000);
    display: grid;
    place-items: center;
    height: 100vh;
    box-shadow: 1px 1px 2px 2px;
}

.container{
    display: grid;
    width: 50%;
    height: 65%;
    background-color: rgb(19 43 43);
    overflow-y: scroll;
    overflow-x: hidden;
    padding: 0px;
}

/* scrollbar */
::-webkit-scrollbar {
    width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
