.navBar{
    width: 50%;
    color: white;
    padding: 3px;
    z-index: 9;
    position: fixed;
}

#player:hover, #scan:hover{
    background-color: #0a5d61;
}

#player{
    padding: 0;
    margin: 0;
}

#scan{
    display: inline-block;
    float: right;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 5px;
    margin: 0;
}