.scan{
    color: white;
    display: grid;
    place-items: center;
    min-height: 450px;
}

.scan > div{
    display: grid;
    place-items: center;
    border-style: solid;
    padding: 10px;
}

.scan > div > h2{
    text-decoration: underline;
}

.scan > div > button{
    color: white;
    background-color: teal;
    border-style: none;
    height: 30px;
    width: 100px;
}

.message{
    min-height: 20px;
}