.MusicList{ 
    margin-top: 160px;
    padding-left: 20px;
    margin-right: 20px;
    height: 100%;
}

.MusicCards{
    padding-left: 10px;
    margin-bottom: 5px;
    box-shadow: 2px 2px 4px;
}

.MusicCards:nth-child(even){
    background-color: rgb(184 195 194);
}

.MusicCards:nth-child(odd){
    background-color: white;
}

.MusicCards:hover{
    background-color: skyblue;
    cursor: pointer;
}

.MusicPath{
  font-size: 13px;
}

.scanMessage{
    color: white;
    text-align: center;
    text-shadow: 2px 2px 2px black;
}