a{
    margin: 5px;
}

audio{
    visibility: hidden;
    width: 0;
    height: 0;
}

.MusicPlayer{ 
    position: fixed;
    width: 50%;
    min-height: 148px;
    background-color: #0b1d1b;
    box-shadow: 1px 1px 3px 1px;
}

.controls{
    place-content: center;
    display: flex;
    padding: 0;
    margin: 0;
}

.controls > a:hover{
    background-color: whitesmoke;
    border-radius: 15px;
}

.timer{
    color: white;
}

.timerSpan{
    padding: 5px;
}

.TrackSlider{
    width: -webkit-fill-available;
    padding: 0;
    margin: 0;
}

.slider:hover {
    opacity: 1; 
}

.slider::-webkit-slider-thumb {
    cursor: pointer;
}

#playerTitle{
    color: white;
    text-align: center;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
    padding-top: 20px;
    margin: 0;
}

#TotalTimeId{
    right: 5px;
    position: absolute;
}
